@import url(https://fonts.googleapis.com/css2?family=Quicksand:wght@300;400;500;600;700&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  background-color: #2e1646;
  font-family: "Quicksand", sans-serif;
}

.app h1 {
  margin: 15px auto;
  text-align: center;
  color: #fff;
  font-size: 1.7rem;
  font-weight: 100;
}

.app p {
  margin: 20px auto;
}

.app p,
.app a {
  color: #ccc;
  text-align: center;
}

@media (max-width: 400px) {
  .app h1 {
    font-size: 1.4rem;
  }
}

.form-container {
  max-width: 650px;
  margin: 0 auto 20px;
  padding: 20px 30px;
  background-color: #fff;
  border-radius: 10px;
  box-shadow: 2px 2px 10px rgba(0, 0, 0, 0.25);
}

.inputs-selector {
  padding-right: 20px;
  border-right: 1px solid #aaa;
}

.inputs-selector label {
  font-size: 1.2rem;
  font-weight: 700;
  margin-right: 5px;
  margin-left: 5px;
}

.inputs-selector input {
  width: 100%;
  margin: 5px auto 20px;
  font-size: 1rem;
  padding: 8px 15px;
  border: 1px solid #aaa;
  border-radius: 50px;
}

.checkbox-selector {
  padding-left: 20px;
}

.checkbox-selector label {
  font-size: 1.2rem;
  font-weight: 500;
  cursor: pointer;
}

.checkbox-selector input {
  margin-right: 8px;
  margin-bottom: 4px;
  vertical-align: middle;
  width: 20px;
  height: 20px;
  cursor: pointer;
}

.checkbox-selector ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

.checkbox-selector ul li {
  margin-bottom: 5px;
}

.checkbox-selector h3 {
  margin-bottom: 10px;
}

.selecting-part {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
}

.spender-selector {
  margin-bottom: 20px;
}

.spender-selector #spender {
  font-size: 1rem;
  padding: 5px 10px;
  border-radius: 50px;
}

.buttons {
  display: flex;
  flex-direction: column;
  margin-top: 20px;
}

.btn {
  border-radius: 50px;
  border: none;
  width: 100%;
  padding: 8px 30px;
  color: #fff;
  font-family: "Quicksand", sans-serif;
  font-size: 1.2rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s;
}

.all-btn {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  background-color: #fff;
  border: 2px solid #333;
  padding: 4px 30px;
  font-size: 1rem;
  color: #333;
  margin-bottom: 5px;
}

.all-btn:hover {
  background-color: #333;
  color: #fff;
}

.submit-btn {
  background-color: #009432;
  margin-bottom: 5px;
}

.submit-btn:hover {
  background-color: #44bd32;
}

.cancel-btn {
  background-color: #c23616;
}

.cancel-btn:hover {
  background-color: #e84118;
}

@media (max-width: 680px) {
  .form-container {
    border-radius: 0;
  }

  .selecting-part {
    flex-direction: column;
  }

  .inputs-selector {
    width: 100%;
    padding-right: 0;
    border: none;
  }

  .inputs-selector #date-picker {
    background-color: #fff;
  }

  .checkbox-selector {
    width: 100%;
    padding-left: 0;
  }

  .checkbox-selector h3 {
    margin-left: 5px;
  }

  .checkbox-selector input {
    margin-left: 10px;
    background-color: #fff;
  }

  /* .btn {
    width: 100%;
    display: block;
  } */

  .all-btn {
    margin-left: 5px;
  }
}

@media (max-width: 360px) {
  .spender-selector select {
    margin-top: 10px;
  }
}

