.card-container {
  max-width: 650px;
  margin: 0 auto;
  padding: 5px;
}

.card {
  position: relative;
  width: 100%;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  border-radius: 8px;
  overflow: hidden;
  box-shadow: 2px 2px 10px rgba(0, 0, 0, 0.25);
  margin-bottom: 5px;
  background-color: #fff;
}

.card-close {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 10px 0;
  box-shadow: 0 1px 10px 2px rgba(0, 0, 0, 0.15);
}

.card-close .row {
  width: 40px;
  text-align: center;
  flex-grow: 0;
}

.card-close .date {
  text-align: center;
  flex-grow: 2;
}

.card-close .name {
  text-align: center;
  flex-grow: 4;
}

.card-close .amount {
  text-align: center;
  flex-grow: 4;
}

.card-close .dropdown-container {
  width: 40px;
  text-align: center;
  flex-grow: 0;
}

.dropdown {
  cursor: pointer;
  transition: -webkit-transform 0.3s;
  transition: transform 0.3s;
  transition: transform 0.3s, -webkit-transform 0.3s;
}

.clicked {
  -webkit-transform: rotate(180deg);
          transform: rotate(180deg);
}

.card-open {
  height: 0;
  transition: all 1s;
}

.card-open.active {
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
}

.card-open-content {
  padding: 15px 20px 5px;
}

.card-open-content div {
  margin-bottom: 10px;
}

hr {
  border: none;
  box-shadow: 0 0 0 0.2px #000;
  margin: 0;
  padding: 0;
  height: 20px;
}

@media (max-width: 400px) {
  .card-close .date {
    display: none;
  }
}

